@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700;800&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*::selection {
  background: #dcb1f9ba;
}

body {
  background: #20232a;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  letter-spacing: 1px;
}

img {
  width: 100%;
}
.container {
  max-width:1224px;
  min-height: 100vh;
  padding: 7rem 1.5rem 0 1.5rem;
  margin: 0 auto;
}

.title {
  text-align: center;
  margin-bottom: 3rem;
}
.title span {
  color: #d479f0;
  text-transform: uppercase;
  font-size: 0.75rem;
  font-weight: 800;
  letter-spacing: 5px;
}

.title span::selection {
  background: #8030b5ba;
}
.title h1 {
  color: #fff;
  font-size: 3rem;
  position: relative;
}
@media screen and (max-width: 500px) {
  .container {
      padding: 7rem 1rem 0 1rem;
  }
  .title h1 {
      font-size: 2.5rem;
  }
}

#phone-num {
  background: none;
  font-size: 1rem;
  transition: 0.3s;
  margin-left: -1.5rem;
  text-decoration: underline;
}

#phone-num:hover {
  color: #d479f0;
}
